@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.mainContainer {
  animation: 1s ease-out 0s 1 fadeIn;
  display: flex;
}

.home {
  display: flex;
  flex-direction: row;
  margin-top: 15px;
}

.errorContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
